import React from "react"
import { graphql } from "gatsby"
import AniLink from "gatsby-plugin-transition-link/AniLink"
import Img from "gatsby-image"
// import { Parallax } from "react-scroll-parallax"

import Style from "./card.module.scss"

export default ({
  node: { previewTitle, previewDescription, strapiId, previewThumbnail },
}) => (
  <div className={Style.cardWrapper}>
    <div className={Style.background}>
        {/* <Parallax y={[-5, 24]}> */}
      <Img
        className={Style.img}
        fluid={previewThumbnail.childImageSharp.fluid}
        alt="test"
      />
      {/* </Parallax> */}
    </div>
    <div className={Style.content}>
        <h2>{previewTitle}</h2>
        <p className={Style.previewDescription}>{previewDescription}</p>
        <AniLink className={Style.primaryButton} paintDrip to={`/portfolio/${strapiId}`} hex="#6236ff">
          Preview
        </AniLink>
    </div>
  </div>
)

export const query = graphql`
  fragment fragmentArticleData on StrapiArticleEdge {
    node {
      id
      previewTitle
      strapiId
      previewDescription
      previewThumbnail {
        childImageSharp {
          fluid(maxWidth: 1200) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
    }
  }
`
