import React from "react"
import { graphql } from "gatsby"

import Style from "./intro.module.scss"
import { ScrollDown } from "../icon/icon"

export default ({ introData: { title, lead } }) => (
  <div className={Style.introWrapper + " mb-2"}>
    <div className="gridContainer">
      <div className="grid-3">
        <div className="col-12_lg-8">
          <h1
            data-sal="slide-up"
            data-sal-delay="500"
            data-sal-easing="ease-in-out-cubic"
            data-sal-duration="1000"
          >
            {title}
          </h1>
          <p
            data-sal="slide-up"
            data-sal-delay="500"
            data-sal-easing="ease-in-out-cubic"
            data-sal-duration="1000"
            className={Style.lead + " mb-1"}
          >
            {lead}
          </p>
          <ScrollDown />
        </div>
      </div>
    </div>
  </div>
)

export const query = graphql`
  fragment fragmentIntroNode on StrapiIntroEdge {
    node {
      title
      lead
    }
  }
`
