import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import Intro from "../components/intro/intro"
import Article from "../components/article/article"

export default ({ data, location }) => (
  <Layout location={location} crumbLabel="Home">
    <section>
      {data.allStrapiIntro.edges.map(({ node }) => (
        <Intro introData={node} key={node.id} />
      ))}
    </section>
    <section
      data-sal="slide-up"
      data-sal-delay="1000"
      data-sal-easing="ease-in-out-cubic"
      data-sal-duration="1750"
    >
      {data.allStrapiArticle.edges.map(({ node }) => (
        <Article articleData={node} key={node.id} />
      ))}
    </section>
  </Layout>
)

export const query = graphql`
  query fetchIntroData {
    allStrapiIntro(filter: { strapiId: { eq: 1 } }) {
      edges {
        ...fragmentIntroNode
      }
    }
    allStrapiArticle {
      edges {
        ...fragmentArticleData
      }
    }
  }
`
